#breadcrumb {
	&.bg-image {
		background-repeat: no-repeat;
		background-size: cover;
		margin-bottom: 20px;
		@media (min-width: 768px) {
			margin-bottom: 35px;
		}
	}
}
.breadcrumb-list {
	display: flex;
    align-items: center;
    padding: var(--gutter-xs) 0;
    overflow: hidden;
    overflow-x: auto;
    >li {
    	line-height: 30px;
    	font-size: calc(var(--body-font-size) - .15rem);
    	a {
    		white-space: nowrap;
    		color: var(--breadcrumb-color-1);
    		&:hover {
    			color: var(--main-color);
    		}
    	}
    	&.active {
    		a {
    			color: var(--breadcrumb-color-2);
    		}
    	}
    }
    .svg-element-ui {
    	padding: 0 8px;
    	svg {
    		width: 9px;
    		height: 9px;
    	}
    }
    @media (min-width: 992px) {
    	padding: var(--gutter-md) 0;
    }
}
.page-heading>div {
	display: flex;
	overflow-y: hidden;
	overflow-x: auto;
	margin-bottom: 20px;
	h1 {
		font-size: var(--heading-size-h2);
	}
	small {
		@include padding-start(10px);
	}
}
.pagination-holder {
	margin: var(--gutter-lg) auto 0;
	.pagination {
		display: flex;
		align-items: center;
		justify-content: center;
		.svg-element-ui {
			svg {
				width: 14px;
				height: 14px;
				fill: currentColor;
			}
		}
		li {
			padding: 0 5px;
			a {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 44px;
				height: 44px;
				border: 1px solid var(--body-border-color);
			}
			&.active, &:hover {
				a {
					background: var(--body-second-color);
					color: var(--text-miscellaneous);
				}
				svg {
					fill: var(--text-miscellaneous);
				}
			}
		}
	}
}