html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-text-size-adjust: 100%;
  font-size: var(--body-base-size);
  font-display: optional;
}

body {
  line-height: 1.4;
  background: var(--body-bg-color);
  text-rendering: optimizeLegibility;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing:antialiased;
  #main-content {
    overflow: hidden;
    min-height: 100vh;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  &:before, &:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
}
:focus {
  outline: none;
}
body, select, input, textarea {
  font-size: var(--body-14);
  font-family: var(--body-font-family);
  font-weight: var(--body-font-weight);
  color: var(--body-color);
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  @media (min-width: 768px) {
    font-size: var(--body-font-size);
  }
}
a {
  text-decoration: none;
  color: var(--body-link);
  transition: all 0.2s ease-in-out 0s;
  &:focus {
    outline: none;
  }

  &:hover {
    @media (hover:hover) {
      text-decoration: none;
    }
  }
}

p {
  margin: 20px 0;
}

ul, ol, dl {
  margin: 0;
}

ul, li, ol {
  list-style: none;
}

ul {
  padding: 0;
  list-style: disc inside none;
}

ol {
  padding: 0;
  list-style: decimal inside none;
}

small {
  font-size: 80%;
}

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -.25rem;
}

sup {
  top: -.5rem;
}

label {
  display: inline-block;
  margin-bottom: 8px;
}
.pointer, .slide_ui-controls--dot button, .tns-nav button {
  cursor: pointer;
}

.clearfix::after {
  display: block;
  clear: both;
  content: "";
}
.req {
  color: red;
}
video {
  max-width: 100%;
}

button {
  border: none;
  outline: none;
  background: none;
  transition: all .35s ease-in-out 0s;

  &:focus {
    -webkit-box-shadow: 0 0 0 #fff;
    box-shadow: 0 0 0 #fff;
  }
}

form {
  display: block;
  margin: 0;
}

select, input, textarea {
  background: none;
  appearance: none;
}

select:focus, input:focus, textarea:focus {
  outline: none;
}
select, input {
  @extend %radius-4px;
  width: 100%;
  border: 1px solid var(--body-border-color);
}

select {
  max-width: 100%;
  padding: 0 var(--gutter-md);
  line-height: 40px;
}

input, textarea {
  padding: 5px 15px;
}

input:focus, textarea:focus {
  border: 1px solid rgba(var(--main-color-rgba), 0.5);
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder, input:-moz-placeholder, textarea:-moz-placeholder, input::-moz-placeholder, textarea::-moz-placeholder, input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  color: #BBBDC3;
}

input {
  height: 40px;
}

textarea {
  @extend %radius-4px;
  width: 100%;
  height: 272px;
  border: 1px solid var(--body-border-color);
}
.shopify-challenge__container .shopify-challenge__button {
  color: var(--btn1-cl);
  background: var(--btn1-bg-cl);
  height: 50px;
}
.overflow-hidden {
