}

.w-100 {
  width: 100%;
}

.h-100 {
  height: 100%;
}

.vertical_align-top {
  align-items: flex-start;
  align-self: flex-start;
}

.vertical_align-middle {
  align-items: center;
  align-self: center;
}

.vertical_align-bottom {
