}
.animation-fade {
  animation: animation-bottom-top 0.6s both;
}
#collections-listing {
  padding: var(--gutter-lg) 0 60px;
  .page-heading {
    margin-bottom: 40px;
  }
  .category {     
    margin-bottom: 18px;
    @extend %transition;
    &:hover {
      @media (hover:hover) {
        box-shadow: 0 0 5px 0 #c6c6c6;
      }
    }
  }
  .collection-title {
    padding: 20px 0;
    font-size: 18px;
    strong {
      font-weight: var(--font-body-weight);
    }
  }
}
#back-to-top {
  @include fixed(n,n,5%,20px);
  z-index: layer(back-to-top);
  @extend %transition;
  opacity: 0;
  visibility: hidden;
  width: 40px;
  padding: 0;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--main-color);
  border-radius: 2px;
  &.show {
    visibility: visible;
    opacity: 1;
    bottom: 60px;
  }
  a {
    padding: 10px;
    svg {
      position: relative;
      top: -1px;
      width: 14px;
      height: 14px;
      path {
        fill: #fff;
      }
    }
  }
}
.price {
  color: var(--price-color);
  font-weight: 500;
  font-size: var(--body-14); 
  @media (min-width: 768px) {
    font-size: var(--product-item-price);
  }
}

img {
  max-width: 100%;

  &.img-lazy {
    position: absolute;
    left: 0;
    top: 0;
  }

  &.lazyload:not([src]) {
    opacity: 0;
  }
}

.image--style {
  position: relative;
  display: block;

  img.img_lazy {
    object-fit: cover;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }
}

.quantity-product input[type="number"] {
  -moz-appearance: textfield;
}

.overlay-ui {
  position: absolute;
  left: 0;
  top: 0;
  word-break: break-word;
}

.f-break {
  padding: 0 var(--gutter-xs);
  width: 100vw !important;
  position: relative;
  left: calc(-1 * (100vw - 100%) / 2);
  flex: 0 0 100vw;
