  }
}
/* filter */
.filter-wrapper {
  .tabs-content_wrapper {
    padding: 30px 25px 35px;
  }
  .tabs-content_item {
    display: none;
    &.active {
      display: block;
    }
    .chosen-btn {
      position: relative;
      margin: 5px 0;
      display: flex;
      width: 100%;
      justify-content: center;
    }
    .btn-reset {
      line-height: 50px;
      text-align: center;
      min-width: 115px;
      text-transform: uppercase;
      border-radius: 4px;
      background: var(--main-color);
      @include margin-start(10px);
    }
  }
  .chosen-wrapper {
    width: 100%;
    >div {
      flex: 1;
    }
  }
  .chosen-container {
    position: relative;
    height: 50px;
    margin: 5px 0;
    pointer-events: none;
    background: var(--miscellaneous-color);
    &.active {
      pointer-events: unset;
      cursor: pointer;
      .chosen-single {
        span {
          opacity: 1;
        }
      }
    }
  }
  .btn {
    height: 50px;
    line-height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    font-weight: 500;
    min-width: 115px;
    border-radius: 4px;
    svg {
      display: none;
      width: 18px;
      height: 18px;
      @include margin-end(10px);
    }
    &:hover {
      svg {
        path {
          fill: var(--btn1-hover-cl); 
        }
      }
    }
  }
  .chosen-single {
    display: flex;
    align-items: center;
    @include padding-end(20px);
    position: relative;
    height: 50px;
    padding: 12px 0 11px;
    &
    >svg {
      @include ab(n,n,n,15px);
      height: 11px;
      width: 11px;
    }
    span {
      display: flex;
      align-items: center;
      height: 100%;
      opacity: 0.5;
      font-size: 15px;
    }
    .chosen-order {
      padding: 0;
      @include margin-end(10px);
      width: 37px;
      opacity: 1;
      justify-content: center;
      border-right: 1px solid var(--body-border-color);
    }
  }
  .chosen-drop {
    display: none;
    background: var(--miscellaneous-color);
    @include ab(49px,0,n,0);
    z-index: layer(chosen-results);
    padding: 4px 5px;
    max-height: 390px;
    overflow-y: auto;
    &.active {
      display: block;
    }
  }
  .chosen-drop::-webkit-scrollbar-track {
    @extend %webkit-scrollbar-track;
  }
  .chosen-drop::-webkit-scrollbar {
    @extend %webkit-scrollbar;
  }
  .chosen-drop::-webkit-scrollbar-thumb {
    @extend %webkit-scrollbar-thumb;
  }
  .chosen-results {
    margin-top: 8px;
    li {
      padding: 4px;
      &:hover, &.result-selected {
        background: #3875d7;
        background-image: linear-gradient(#3875d7 20%,#2a62bc 90%);
        color: #fff;
      }
    }
  }
  .chosen-search {
    input {
      height: 25px;
      padding: 4px;
      border: 1px solid #aaa;
      border-radius: 0;
    }
  }
  .search {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    margin: 5px 0;
    input {
      height: 50px;
      background: var(--miscellaneous-color);
      border-radius: 4px;
      margin-bottom: 5px;
    }
    button {
      margin: 5px 0;
    }
  }
  @media (min-width: 768px) {
    .tabs-content {
      width: 70%;
      margin: 0 auto;
    }
    .chosen-container {
      flex: 1;
      margin: 5px 3px;
    }
  }
  @media (min-width: 992px) {
    .tabs-content {
      max-width: 1020px;
      width: 100%;
    }
    .chosen-wrapper {
      display: flex;
      flex-wrap: wrap;
    }
    .tabs-content_wrapper {
      .chosen-container {
        &:first-child {
          margin-inline-start: 0;
          border-radius: 4px 0 0 4px;
        }
        &.chosen-is-opening {
          cursor: pointer;
          border-bottom-left-radius: 0px;
        }
      }
      .btn {
        position: relative;
        border-radius: 0;
        @include margin-start(3px);
        svg {
          display: block;
        }
      }
    }
    .tabs-content_item {
      .btn-reset {
        border: 1px solid var(--main-color);
        margin-left: 4px;
        padding: 0 20px;
        min-width: auto;
        height: 50px;
        border-radius: 0 4px 4px 0;
      }
    }
  }
}
/* end filter */
.is-sliding {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.tns-inner .tns-item {
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.media-thumbnails:not(.tns-carousel), .media-main:not(.tns-carousel) {
