}
.slide-flex-preload.slide_ui-element:not(.tns-slider) {
  display: flex;
}
.section_content-body .tns-inner .product-section {
  @include padding-start(0);
}

.boxed-wapper {
  iframe {    
    min-width: 100%;
    min-height: 100%;
    max-width: 100%;
    max-height: 100%;
  }
}
.as-track-button .aftership-poweredBy {
  margin-top: 15px;
}

/*pangination*/
.slideshow-dot {
  @include ab(n,n,20px,n);
  z-index: 11;
  width: 100%;
  display: flex;
  justify-content: center;
  a {
    margin: 0 5px;
    .circle {
      width: 16px;
      height: 16px;
      @extend %radius-circle;
      border: 2px solid var(--body-bg-color);
      display: block;
      box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.5);
    }
    &.tns-nav-active .circle {
      box-shadow: none;
      border-color: var(--main-color);
    }
    &:hover, &.tns-nav-active {
      .circle {
        background: var(--main-color);
      }
    }
  }
}
/*end pangination*/

/*tns control*/
.js-slider-controls {
  display: none; 
  @media (min-width: 768px) {
    display: block;
  }
  a {
    z-index: layer(js-tns-controls);
    width: 35px;
    height: 35px;
    display: flex;
    margin-top: 10px;
    align-items: center;
    justify-content: center;
    top: 0%;
    background: var(--miscellaneous-color);
    path {
      stroke: var(--body-second-color);
    }
    &[aria-disabled="true"] {
      opacity: 0.7;
    }
  }
  .js-prev-button {
    @include ab(n,n,n,45px);
    svg {
      @include margin-start(-1px);
    }
  }
  .js-next-button {
    @include ab(n,n,n,0);
  }
}
.tns-nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 10px 0;
  height: 15px;
  button {
    width: 12px;
    height: 12px;
    margin: 2px 7px;
    border: 2px solid var(--miscellaneous-color);
    background: var(--body-second-color);
    @extend %radius-circle;
    position: relative;
    &:hover, &.tns-nav-active {
      border: 2px solid var(--body-second-color);
    }
  }
}
/*end tns control*/

/*featured categories*/
.featured_block-category {
  .menu_list-wrapper {
    li {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      padding: 6px 0;
      a {
        line-height: 23px;
        font-size: var(--body-14);
        color: var(--body-second-color);
      }
      .number {
        background: var(--body-border-color);
        font-size: 12px;
        padding: 2px 3px;
        border-radius: 2px;
        margin-left: 12px;
      }
      .expand {
        position: relative;
        display: inline-block;
        margin-top: -2px;
        width: 18px;
        height: 18px;
        cursor: pointer;
        &:after {
          content: '';
          @include ab(8px,n,n,0);
          display: inline-block;
          width: 10px;
          height: 2px;
          background-color: var(--body-second-color);
        }
        &:before {
          content: '';
          @include ab(4px,n,n,4px);
          display: inline-block;
          transition: all .2s ease-out;
          width: 2px;
          height: 10px;
          background-color: var(--body-second-color);
        }
        &.is-open {
          &:before {
            transform: rotate(90deg);
          }
        }
      }
      .dropdown-menu {
        width: 100%;
        transition: 0.2s all ease;
        overflow: hidden;
        display: none;
        li {
          @include padding(6px, 0, 6px, 18px);
          a {
            &:before {
              content: "-";
              display: inline-block;
              width: 15px;
            }
          }
        }
        &.is-open {
          display: block;
        }
      }
    }
  }
}
/*end featured categories*/

/*featured product*/
.product-card {
  background: var(--miscellaneous-color);
  padding: 10px;
  position: relative;
  height: 100%;
  @media (min-width: 768px) {
    padding: 15px 25px;
  }
  .imageCrop-true {
    img {
      object-fit: cover;
    }
  }
  .product-card_header {
    margin-bottom: 20px;
    position: relative;
  }
  &.balance-row-1 {
    .product-card_name {
      @include clamp(1);
    }
  }
  &.balance-row-2 {
    .product-card_name {
      @include clamp(2);
      min-height: calc(1.1em * 2);
    }
  }
  &.balance-row-3 {
    .product-card_name {
      @include clamp(3);
      min-height: calc(1.1em * 3);
    }
  }
  .countdown {
    @include ab(n,0,0,0);
    z-index: layer(countdown-block);
    display: flex;
    .countdown_item {
      flex: 1;
      background: rgba(var(--main-color-rgba), 1);
      padding: 3px;
      @include border-inline-end(1px solid var(--body-border-color));
      >span {
        display: block;
        text-align: center;
        font-size: var(--body-14);
        font-weight: 500;
      }
      .label {
        font-size: 10px;
      }
    }
    @media (min-width: 768px) {
      .countdown_item {
        padding: 5px 3px 6px;
        >span {
          font-size: 17px;
        }
      }
    }
    @media (min-width: 1200px) {
      .countdown_item {
        padding: 6px 3px;
        >span {
          font-size: 20px;
          line-height: 22px;
        }
        .label {
          line-height: 14px;
        }
      }
    }
  }
  .product-card_image {
    img {
      @extend %transition;
    }
  }
  .featured_product-overlay { 
    @include ab(0,auto,n,0);
    z-index: layer(featured_product-overlay);
    .card-button { 
      @extend %transition;
      @media (min-width: 1025px) {
        opacity: 0;
        visibility: hidden;
        transform: scale(0.5,0.5);
      }
      a {        
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 5px;
      }
      &:hover {
        @media (hover:hover) {          
          a {
            color: var(--body-link);
          }
        } 
      }
    }
  }
  &:hover {
    @media (hover:hover) {      
      box-shadow: 0 0 8px 1px rgba(0,0,0,.05);
      .featured_product-overlay {
        .card-button {
          opacity: 1;
          visibility: visible;
          transform: scale(1,1);
        }
      }
      .hover-2 .product-card_image {
        img {
          transform: scale(1.08,1.08);
        }
      }
      .hover-3 .product-card_image {
        img {
          filter: blur(1.5px);
        }
      }
      .product-card_footer { 
        .product-card_action {
          .btn svg {
            fill: var(--main-color);
          }
        }
      }
    }
  }
  .add-to-wishlist {
    &.arn_added {
      path {
        fill: var(--main-color);
      }
    }
  }
  .add-to-compare {
    &.arn-cp-added {
      background: none;
    }
    &.arn_added {
      path {
        fill: var(--main-color);
      }
    }
  }
}

.product-card_footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  min-height: 32px;
  p {
    margin: 0;
  }
  .product-card_action {
    .btn {
      padding: 0;
      svg {
        fill: var(--body-second-color);
        color: var(--body-second-color);
        width: 24px;
        height: 24px;
      }
    }
  }
}
.product-card_name {
  margin: 0 0 12px;
  font-size: var(--body-14);
  @media (min-width: 768px) {
    font-size: var(--product-item-name);
  }
}
.product-card_review {
  >span:not(:empty) {
    margin-bottom: var(--gutter-xs);
  }
}
.product-card_vendor {
  text-transform: uppercase;
  font-weight: 500;
  display: block;
  margin-bottom: 5px;
  a {
    color: var(--body-second-color);
  }
}
/*end featured product*/

/*featured collection*/
.featured_block .sb-title {
  margin: 0 0 20px;
  font-size: var(--heading-size-h5);
  text-transform: uppercase;
  border-bottom: 1px solid var(--body-border-color);
  padding: 20px 0 15px;
  font-weight: 500;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .filter-clear {
    text-transform: capitalize;
    font-weight: var(--body-font-weight);
    span:before {
      bottom: -18px;
    }
    svg {
      height: 18px;
    }
  }
}
/*end featured collection*/

/*label*/
.label_ui-group {
  @include ab(0,-14px,n,n);
  @media (min-width: 768px) {
    @include ab(n,-29px,n,n);
  }
  >span {
    border-radius: 0;
    padding: 0 10px;
    line-height: 20px;
    position: relative;
    margin-bottom: 7px;
    &:before {
      content: '';
      width: 0;
      height: 0;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
      @include ab(100%,0,n,n);
      @include rotate(45deg);
      margin-top: -1px;
    }
  }
  .label_ui-sale:before {
    border-bottom: 4px solid var(--sale-lb-bgcl);
  }
  .label_ui-hot:before {
    border-bottom: 4px solid var(--hot-lb-bgcl);
  }
  .label_ui-new:before {
    border-bottom: 4px solid var(--new-lb-bgcl);
  }
}
.label_ui {
  line-height: 17px;
  padding: 2px var(--gutter-xxs);
  margin: 0 0 5px;
  font-size: 10px;
  text-align: center;
  text-transform: uppercase;
  display: block;
}
.label_ui-hot {
  color: var(--hot-lb-cl);
  background: var(--hot-lb-bgcl);
}
.label_ui-new {
  color: var(--new-lb-cl);
  background: var(--new-lb-bgcl);
}
.label_ui-sale {
  color: var(--sale-lb-cl);
  background: var(--sale-lb-bgcl);
}
/*end label*/

/*widget social*/
.widget-social-icons {
  display: flex;
  li {
    @include margin-end(5px);
    svg {
      width: 40px;
      height: 40px;
      display: block;
    }
  }
  .svg-facebook-square {
    path {
      fill: #3b5a9a;
    }
  }
  .svg-instagram-square {
    path {
      fill: #b22e9b;
    }
  }
  .svg-pinterest-square {
    path {
      fill: #e11a21;
    }
  }
  .svg-youtube-square {
    path {
      fill: #d72928;
    }
  }
  .svg-twitter-square path {
    fill: #28a9e2;
  }
  .svg-vimeo-square {
    color: #0facb6;
  }
}
/*end widget social*/

/*modal*/
.modal {
  display: flex;
  align-items: center;
  @include fixed(0,0,0,0);
  margin: 0 20px;
  visibility: hidden;
  opacity: 0;
  transition: 0.2s all ease-in-out;
  z-index: layer(modal-cart-notify);
  &.is-open {
    opacity: 1;
    visibility: visible;

    .modal_box {
      transform: scale(1);
    }
  }
  .modal_box {
    transform: scale(0.8);
  }
  .modal_wrapper {
    margin: 0 auto;
  }
  .modal_close {
    @include ab(0,n,n,0);
    width: 32px;
    height: 32px;
    background: var(--main-color);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: var(--text-miscellaneous);
    svg {
      color: var(--text-miscellaneous);
      width: 11px;
      height: 11px;
    }
  }
  .modal_overlay {
    background: rgba(0,0,0,0.7);
    height: 100%;
    width: 100%;
    @include fixed(0,0,n,n);
  }
  .modal_box {
    background: var(--miscellaneous-color);
    padding: 20px;
    box-shadow: 0 0 7px 5px rgba(0,0,0,0.3);
    width: 100%;
    max-height: 90vh;
    overflow-y: auto;
    margin: 0 auto;
    position: relative;
    transition: 0.2s all ease-in-out;
  }
  @media (min-width: 768px) {
    .modal_close {
      top: -32px;
    }
  }
}
/*end modal*/

/*currency-language*/
.modal-currency-vs-language {
  .modal_box {
    width: 280px;
    font-weight: 500;
    overflow: visible;
    padding: 30px 25px;
    @media (min-width: 375px) {
      width: 320px;
    }
    * {
      color: var(--body-color);
    }
    svg path {
      fill: var(--body-color);
    }
    select {
      padding: 0 10px;
    }
    .select-group {
      position: relative;
      margin-bottom: 30px;
      svg {
        @include ab(n,n,n,10px);
        width: 9px;
        height: 100%;
        pointer-events: none;
      }
    }
    .modal_close {
      svg {
        width: 11px;
        height: 11px;
      }
    }
  }
  .modal_header {
    font-size: var(--heading-size-h4);
    margin-bottom: 15px;
    padding-bottom: 12px;
    border-bottom: 1px solid var(--body-border-color);
  }
  .modal_content {
    label {
      text-transform: capitalize;
      font-size: var(--heading-size-h5);
    }
    >div:last-child {
      margin-bottom: 0;
    }
  }
}
/*end currency-language*/

/*cart notify*/
.modal-cart-notify {
  .headline {
    margin: 0 0 30px;
    @include padding-end(15px);
    svg {
      width: 12px;
      height: 12px;
      @include margin-end(7px);
      position: relative;
      top: 1px;
    }
  }
  .modal_product {
    display: flex;
    margin-bottom: 25px;
    .modal_product_image {
      align-items: center;
      @include margin-end(10px);
      width: 100px;
      img {
        width: 100px;
      }
    }
    .modal_product_title {
      font-size: var(--product-item-name);
      font-weight: 500;
      margin: 0 0 8px;
    }
    .modal_product_variant {
      margin-bottom: 8px;
      font-size: 15px;
    }
    .modal_product_qty {
      font-size: 15px;
      margin-bottom: 8px;
      strong {
        @include margin-end(8px);
      }
    }
    .option {
      display: block;
      color: var(--color-second);
      font-size: var(--body-14);
      margin-top: 8px;
    }
    .modal_product_price {
      font-size: 15px;
      strong {
        @include margin-end(8px);
      }
    }
  }
  .modal_action {
    padding: 20px 0 10px;
    border-top: 1px solid var(--body-border-color);
    .modal_number {
      font-size: 15px;
      display: inline-block;
      margin-bottom: 8px;
      @include padding-end(15px);
    }
    .modal_subtotal {
      display: inline-block;
      font-size: var(--product-item-price);
      margin-bottom: 15px; 
      strong {
        @include margin-end(15px);
      }
    }
  }
  .wrap-progress {
    margin: 30px 0;
  }
  .modal_button {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .modal_continue {
      margin-bottom: 20px;
    }
    >div {
      width: 100%;
      text-align: center;
      a {
        width: 100%;
        height: 50px;
        padding: 0 30px;
        line-height: 50px;
      }
    }
  }
  @media (min-width: 768px) {
    .modal_box {
      padding: 30px;
      width: 680px;
      overflow: visible;
    }
    .modal_product {
      .modal_product_image {
        @include margin-end(30px);
        width: 120px;
        min-height: 126px;
        img {
          width: 120px;
        }
      }
    }
    .modal_action {
      .modal_number {
        margin-bottom: 15px;
        width: 49.5%;
      }
      .modal_subtotal {
        width: 49.5%;
        text-align: right;
      }
    }
    .modal_button {
      flex-wrap: nowrap;
      .modal_continue {
        margin-bottom: 0;
      }
      >div {
        width: auto;
      }
    }
  }
}
/*end cart notify*/

/*popup newsleter*/
.modal-newsletter {
