      padding: 35px 0;
    }
  }
  .form_wrapper {
    overflow-y: auto;
    max-height: calc(100vh - 336px);
  }
  &.is-open {
    .lazy-drawer-cart {
      transform: translateX(0);
      transition: 0.3s all ease-in-out;
    }
    .cart-list {
      @extend %animation-active;
    }
    .drawer_overlay {
      width: 100vw;
      height: 100vh;
