      width: 13px;
      height: 13px;
    }
  }
  .drawer_header {      
    position: relative;
    display: block;
    padding: 13px 15px;
    background: var(--main-color);
    text-transform: uppercase;
    font-weight: 500;
  }
  .cart-item {
