  .subtotal {
    padding: 20px 0;
    margin-top: 20px;
    color: var(--color-second);
    border-top: 1px solid var(--body-border-color);
    font-size: 15px;
    display: flex;
    justify-content: space-between;
    .money {
      font-size: 18px;
    }
  }
  .action {
    .action_buttons {
      display: flex;
      justify-content: space-between;
      padding-bottom: 10px;
      a {
        width: 45%;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 8px 32px;
        border-radius: 3px;
      }
    }
  }
  @media (min-width: 1025px) {
    display: flex;
  }
}
/*end cart dropdown*/

/*page 404*/
.page-404 {
  .page-content {
    margin: 0 auto;
    max-width: 460px;
  }
  .page-text-content {
    margin: 30px 0 60px;
    h1 {
      font-size: 130px;
      font-weight: 400;
      color: #d0d0d0;
      margin-bottom: 25px;
      line-height: 1.2;
    }
    h2 {
      line-height: 1.65;
      font-size: 24px;
    }
    p {
      margin: 0 0 45px;
      font-size: var(--body-14);
      color: var(--color-second);
      line-height: 1.9;
    }
  }
  .form-group {
    display: flex;
    input {
      background: var(--miscellaneous-color);
    }
    .btn {
      @include margin-start(12px);
    }
  }
  .homepage-redirect {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 30px 0 60px;
    .svg_element-ui {
      line-height: 10px;
      @include margin-end(5px);
    }
    svg {
      width: 11px;
      height: 11px;
    }
    &:hover {
      @media (hover:hover) {  
        color: var(--main-color);
        svg {
          fill: var(--main-color);
        }
      }
    }
  }
  @media (min-width: 768px) {
    .page-text-content {
      h1 {
        font-size: 180px;
      }
      h2 {
        font-size: 36px;
      }
      p {
        font-size: 18px;
      }
    }
  }
}
/*end 404*/

/* sidebar */
@media (min-width: 992px) { 
  div #sidebarDrawer {
    position: unset;
    width: 100%;
    height: auto;
    transition: none;
    margin-bottom: 30px;
    .section_content-wrapper {
      padding: 0;
    } 
    .section_content-body {
      overflow: hidden;
      max-height: 100%;
      padding: 0;
    }
  }
}
/* end sidebar */